<template>
  <v-container>
    <v-row>
      <v-col cols="12" md="12">
        <v-data-table
          :headers="headers"
          :items="student"
          class="elevation-1"
          :loading="loading1"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <v-spacer></v-spacer>
              <v-dialog v-model="dialog" max-width="500px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    color="primary"
                    dark
                    class="mb-2"
                    v-bind="attrs"
                    v-on="on"
                  >
                    Add Student
                  </v-btn>
                </template>
                <v-card>
                  <v-card-title>
                    <span class="text-h6 ml-2">{{ formTitle }}</span>
                  </v-card-title>

                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="12" md="6">
                          <v-text-field
                            v-model="editedItem.firstName"
                            label="First Name"
                            outlined
                            dense
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="6">
                          <v-text-field
                            v-model="editedItem.lastName"
                            label="Last Name"
                            outlined
                            dense
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="6">
                          <v-text-field
                            v-model="editedItem.email"
                            label="Email"
                            outlined
                            type="email"
                            dense
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="6">
                          <v-text-field
                            v-model="editedItem.age"
                            label="Age"
                            outlined
                            type="number"
                            dense
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="6">
                          <v-select
                            :items="items"
                            v-model="editedItem.gender"
                            outlined
                            dense
                            label="Gender"
                          ></v-select>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">
                      Cancel
                    </v-btn>
                    <v-btn
                      color="blue darken-1"
                      text
                      @click="save"
                      :loading="loading2"
                    >
                      Save
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialogDelete" max-width="500px">
                <v-card>
                  <v-card-title class="text-h5"
                    >Are you sure you want to delete this Student?</v-card-title
                  >
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="closeDelete"
                      >Cancel</v-btn
                    >
                    <v-btn color="primary" text @click="deleteItemConfirm"
                      >YES</v-btn
                    >
                    <v-spacer></v-spacer>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
          <template v-slot:[`item.actions`]="{ item }">
            <v-btn small color="primary" class="mr-2"> view </v-btn>

            <v-btn small color="primary" class="mr-2" @click="editItem(item)">
              edit
            </v-btn>
            <v-btn small color="primary" @click="deleteItem(item)">
              delete
            </v-btn>
          </template>
          <template v-slot:[`item.name`]="{ item }">
            <span>{{ item.firstName }} {{ item.lastName }}</span>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
// @ is an alias to /src
import axios from "axios";
export default {
  name: "Home",
  components: {},
  data: () => ({
    dialog: false,
    loading1: false,
    loading2: false,
    dialogDelete: false,
    items: ["male", "female"],
    headers: [
      {
        text: "Name",
        align: "start",
        sortable: false,
        value: "name",
      },
      { text: "Email", value: "email", sortable: false },
      { text: "Gender", value: "gender", sortable: false },
      { text: "Age", value: "age", sortable: false },
      { text: "Actions", value: "actions", sortable: false },
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      firstName: "",
      lastName: "",
      email: "",
      age: 0,
      gender: "",
      id: 0,
    },

    student: [],
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New Student" : "Edit Student";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  methods: {
    fetchStudent() {
      this.loading1 = true;
      axios
        .get("http://localhost:3000/student", {
          headers: {
            "Content-Type": "application/json",
          },
        })
        .then((res) => {
          this.loading1 = false;
          this.student = res.body;
        })
        .catch((err) => console.log(err));
    },

    editItem(item) {
      this.editedIndex = this.student.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.student.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.student.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.student[this.editedIndex], this.editedItem);
      } else {
        this.loading2 = true;
        fetch("http://localhost:3000/student", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            firstName: this.editedItem.firstName,
            lastName: this.editedItem.lastName,
            email: this.editedItem.email,
            age: JSON.parse(this.editedItem.age),
            gender: this.editedItem.gender,
            id: 0,
          }),
        })
          .then((res) => {
            this.loading2 = false;
            console.log(res);
            this.student.push(this.editedItem);
          })
          .catch((err) => {
            console.log(err);
            this.loading2 = false;
          });
      }
      this.close();
    },
  },
  mounted() {
    this.fetchStudent();
  },
};
</script>
